.sweet-alert {
  background-color: #fff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  border-radius: 5px;
  box-sizing: border-box;
  text-align: center;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-top: -200px;
  max-height: 90%;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
  z-index: 2000; }
  .sweet-alert:focus {
    outline: 0; }
  .sweet-alert button {
    margin: 0 5px;
    padding: 8px 16px; }
  .sweet-alert .form-control {
    max-width: 300px;
    margin: 0 auto; }

.sweet-alert h2 {
  color: #575757;
  text-align: center;
  text-transform: none;
  position: relative;
  padding: 0;
  display: block; }

.sweet-alert p {
  text-align: center;
  position: relative;
  float: none;
  margin: 0;
  padding: 0; }

.sweet-alert hr {
  height: 5px;
  color: transparent;
  border: 0; }

.sweet-alert button.styled {
  color: #fff;
  border: 0;
  box-shadow: none;
  font-size: 17px;
  font-weight: 500;
  border-radius: 5px;
  padding: 10px 32px;
  margin: 0 5px;
  cursor: pointer; }

.sweet-alert button.styled:focus {
  outline: none; }

.sweet-alert button.styled.sweet-cancel[disabled] {
  opacity: .4; }

.sweet-alert button.styled.sweet-confirm[disabled] {
  border: 4px solid transparent;
  border-color: transparent;
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 0 30px;
  vertical-align: middle;
  background-color: transparent !important;
  color: transparent;
  cursor: default;
  border-radius: 100%;
  -webkit-animation: rotate-loading 1.5s linear 0s infinite normal;
  animation: rotate-loading 1.5s linear 0s infinite normal; }

.sweet-alert button.styled::-moz-focus-inner {
  border: 0; }

.sweet-alert[data-has-cancel-button=false] button {
  box-shadow: none !important; }

.sweet-alert .icon {
  width: 80px;
  height: 80px;
  border: 4px solid #808080;
  border-radius: 50%;
  margin: 20px auto;
  padding: 0;
  position: relative;
